/* Variables */
a:hover {
  font-weight: bold;
  color: #4286F4;
  border-bottom: 1px solid #999; }

a:visited {
  color: #551A8B; }

/* Mixins */
/* Helper Classes */
/* Font Styles / Modifiers */
.bold-text {
  font-weight: bold; }

.italic-text {
  font-style: italic; }

/* Text Formatting  */
.tab {
  text-indent: 40px; }

/* Text Color */
.white-text {
  color: #FFF; }

/* Navigation Styles */
.header-color {
  background: #373B44;
  /* fallback for old browsers */
